body {
  background-color: rgb(244, 244, 244);
  color: #333;
  font-size: 10px;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

p {
  font-size: 1.6rem;
}
small {
  font-size: 1.2rem;
}
.container {
  margin:0 auto;
  max-width: 1000px;
  padding: 40px;
}

.title {
  font-size: 3rem;
  text-align: center;
}

.form {
  display: grid;
  padding-bottom: 2rem;
}
.label {
  font-size: 1.2rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
}
.input {
  font-size: 1.4rem;
  padding: 0.3rem 1.5rem;
  line-height: 2.8rem;
  border-radius: 20px;
  border: 1px solid #ddd;
  margin-bottom: 1rem;
}
.button {
  background-color: rgba(0,0,0,0.75);
  color: #fff;
  padding: .3rem 1.3rem;
  border: 1px solid rgba(0,0,0,0.75);
  border-radius: 20px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background-color 250ms;
}

.button:hover {
  background-color: rgba(0,0,0,0.85);
}


@media (min-width: 786px) {
	.form {
		grid-template-columns: auto 1fr auto;
		grid-gap: 1rem;
		align-items: center;
	}

	.input {
		margin-bottom: 0;
	}
}